import Store from '../Store';

export default class ReviewStore extends Store {
  constructor() {
    super();

    this.review = {
      rate: 5,
      comment: '',
    };

    this.reviewId = null;

    this.errors = {
      comment: '',
      submit: '',
      orderNumber: '',
      productId: '',
    };

    this.errorMessage = {
      comment: ' 5   ',
      submit: '       ',
      orderNumber: ' ',
      productId: ' ',
    };
  }

  changeRate(rate) {
    this.review = { ...this.review, rate };

    this.publish();
  }

  changeComment({ comment }) {
    this.review = { ...this.review, comment };

    this.publish();
  }

  hasError() {
    const errors = Object.values(this.errors).filter((error) => error);

    return !!errors.length;
  }

  getError() {
    return Object.values(this.errors).filter((error) => error).at(0);
  }

  clear() {
    this.reviewId = null;

    this.review = {
      rate: 5,
      comment: '',
    };

    this.errors = {
      comment: '',
      submit: '',
      orderNumber: '',
      productId: '',
    };
  }
}

export const reviewStore = new ReviewStore();
