import styled from 'styled-components';

const OPTIONS = [
  { value: '', name: '  .' },
  { value: '    .', name: '    .' },
  { value: '  .', name: '  .' },
  { value: '   .', name: '   .' },
  { value: ' ', name: ' ' },
];

const Select = styled.select`
  width: 100%;
  padding: .7em;
  margin-bottom: .7em;

  :focus {
    outline: none;
  }
`;

function ShippingMessage({ handleChange }) {
  return (
    <Select onChange={(e) => handleChange(e.target.value)}>
      {OPTIONS.map((option) => (
        <option
          key={option.name}
          value={option.value}
        >
          {option.name}
        </option>
      ))}
    </Select>
  );
}

export default ShippingMessage;
